import React, {Component} from 'react';
import { connect } from 'react-redux';
import './App.css';
import ButtonMenus from "./components/ButtonMenus/ButtonMenus"

class App extends Component{

  state = {
    names: [
      'Vic',
      'Vivian',
      'Vicki',
      'Venessa',
      'Forbes',
      'Vincent'
    ],
    pageActive: "/",
    activePages:[
      {
        function:'countdown',
        label: 'Count Down',
        path: '/countdown'
      },
      {
        function:'manageNames',
        label: 'Manage Names',
        path: '/manages-names'
      }
    ]
  }

  


  render(){
    const length = this.props.names.length - 1
    const randomNumber = parseInt(Math.random() * length)
    return (
      <div className="App">
        <header className="App-header">
          <h1>Hello...{this.props.names[randomNumber]}</h1>
          <ButtonMenus/>
          
        </header>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return{
    names: state.names
  };
};

export default connect(mapStateToProps)(App);
